
.feature-item {
 
  align-items: center;

  display: flex;
  flex-direction: column;
 
  gap: 1.2rem;
 
  margin: 0 auto;
  padding: 2.5rem;
 
  width: 33%;

 
  p {
    width: auto;
  }

  // Média-query pour les écrans de petite taille
  @media (max-width: 920px) {
    width: 100%; // Largeur de l'élément à 100%
  }


  &-icon {
    border: 10px solid #00bc77;
    border-radius: 50%;
    padding: 1rem;
    width: 9.375rem;
  }

  
  &-title {
    color: #222;
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
}

// Média-query pour les écrans de petite taille
@media (max-width: 920px) {
  .feature-item {
    width: auto; 
  }
}

 