* {
  font-family: Avenir, Helvetica, Arial, sans-serif;
}
header {
  & nav {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding: 0.3125rem 1.25rem;
    & img {
      max-width: 100%;
      width: 12.5rem;
    }
    & a {
      color: #2c3e50;
      display: flex;
      margin-right: 0.5rem;
      text-decoration: none;
      &:hover{
        text-decoration: underline;
      }
      & p {
        font-weight: bold;
      }
      & i {
        font-size: 1.1rem;
        margin-right: 0.5rem;
      }
    }
    .connected {
      display: flex;
      gap: 1rem;
      margin-right: 0.5rem;
      & p {
        font-weight: 700;
      }
      & i {
        font-size: 1.1rem;
        margin-right: 0.5rem;
      }
    }
    .not-connected {
      display: flex;
      align-items: center;
      gap: 10px;
      & p {
        font-weight: 700;
      }
    }
  }
}
