.account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
  flex-direction: row;
  padding: 1.5rem;
  box-sizing: border-box;
  text-align: left;
  margin-bottom: 2rem;

  @media screen and (max-width: 680px) {
    
  }

  &-amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold;

    &-description {
      margin: 0;
    }
    @media screen and (max-width: 680px) {
      font-size: 40px;
    }
  }

  &-title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
  }

  &-content-wrapper {
    width: 100%;
    flex: 1;
  }

  .transaction-button {
    display: block;
    width: 45%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;

    @media screen and (max-width: 680px) {
      width: 100%;
    }
  }

  .cta {
    justify-content: flex-end;
    display: flex;
  }

  @media screen and (max-width: 680px) {
    flex-direction: column;
    align-items: stretch;
    
  }
}
